isLoaded:boolean = false;

constructor(public db:AngularFireDatabase){
    this.isLoaded = false;
    this.courseListRef = this.db.list('/courses');
    this.courseList = this.courseListRef.valueChanges();
    this.courseList.subscribe(res =>{
        this.courses = res;
        // console.log(res);
        this.isLoaded = true;
    });
    // console.log(this.courses);
}

