import { Component, OnInit } from '@angular/core';
import { CourseService } from '../data.service';
import { SearchService } from '../search.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css'],
  providers: [CourseService]
})
export class DashboardComponent implements OnInit {
  searchValue: string = '';
  searchedCourses: object[] = [];

  constructor(private courseService: CourseService, private searchService: SearchService, public router: Router) { }

  ngOnInit() {

  }

  ngDoCheck(){
      this.searchedCourses = [];
      // console.log(this.searchValue);
      for(var i:number = 0; i<this.courseService.courses.length;i++){
          if((this.courseService.courses[i].name.toUpperCase().includes(this.searchValue.toUpperCase())) ||
             (this.courseService.courses[i].fullname.toUpperCase().includes(this.searchValue.toUpperCase()))){
              // console.log("result: "+this.courseService.courses[i].fullname.toUpperCase());
              // console.log("search: "+this.searchValue.toUpperCase());
              this.addSearchedCourse(this.courseService.courses[i]);
          }
      }

      this.searchService.update(this.searchedCourses);

      if(!this.isEmpty(this.searchService.getCategoryCourses())){
        this.searchService.update(this.searchService.getCategoryCourses());
        this.searchService.updateCategory(this.searchedCourses);
      }
  }

  addSearchedCourse(obj: object){
      this.searchedCourses.push(obj);
  }

    isEmpty(obj: object) {
        for(var key in obj) {
            if(obj.hasOwnProperty(key))
                return false;
        }
        return true;
    }

}
