
export class CourseDataService{
    courseObject: object = {};
    selectedName: string;
    loggedIn: boolean = false;
    deleteObject: object = {};
    currentUser:any;

    constructor(){}

    getObject(){
        return this.courseObject;
    }

    updateObject(obj: object){
        this.courseObject = obj;
        // console.log(this.courseObject);
    }

    getDelete(){
        return this.deleteObject;
    }

    updateDelete(obj: object){
        this.deleteObject = obj;
        // console.log(this.courseObject);
    }

    getName(){
        return this.selectedName;
    }

    updateName(name:string){
        this.selectedName = name;
    }

    updateLogged(log:boolean){
        this.loggedIn = log;
    }

    getLogged(){
        return this.loggedIn;
    }

    getUser(){
        return this.currentUser;
    }

    updateUser(user:any){
        this.currentUser = user;
    }
}
