import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CourseDataService } from '../dashboard/course-list/course-data.service'
import { AngularFireDatabaseModule, AngularFireDatabase } from 'angularfire2/database';
import { AngularFireAuth } from 'angularfire2/auth';
import * as firebase from 'firebase';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
authState: any = null;
isLoggedin:boolean = false;

  constructor(public router: Router,
              private data: CourseDataService,
              private afAuth: AngularFireAuth,
              private db:AngularFireDatabase) {
  }

  ngOnInit(){
      this.afAuth.authState.subscribe((auth)=>{
          this.authState = auth;
      });

      this.data.updateLogged(this.isLoggedin);

  }

  ngDoCheck(){
      // console.log("Is logged in: " + this.authenticated());
      // console.log("Current user: " + this.currentUser());
      this.isLoggedin = this.authenticated();
      this.data.updateLogged(this.authenticated());

      if(this.authState){
        this.data.updateUser(this.currentUser().email);
      }

  }

  signInWithFacebook(){
      const provider = new firebase.auth.FacebookAuthProvider()
      return this.socialSignIn(provider);
  }

    private socialSignIn(provider) {
    return this.afAuth.auth.signInWithPopup(provider)
      .then((credential) =>  {
          this.authState = credential.user
          console.log(this.authState);
      })
      .catch(error => console.log(error));
  }

  signOut(): void {
    this.afAuth.auth.signOut();
    this.router.navigate(['/'])
  }

  authenticated(): boolean {
    return this.authState !== null;
  }

  // Returns current user data
  currentUser(): any {
    return this.authenticated ? this.authState : null;
  }

  // Returns
  get currentUserObservable(): any {
    return this.afAuth.authState
  }

  // Returns current user UID
  currentUserId(): string {
    return this.authenticated ? this.authState.uid : '';
  }

  // Anonymous User
  currentUserAnonymous(): boolean {
    return this.authenticated ? this.authState.isAnonymous : false
  }

  // Returns current user display name or Guest
  currentUserDisplayName(): string {
    if (!this.authState) { return 'Guest' }
    else if (this.currentUserAnonymous) { return 'Anonymous' }
    else { return this.authState['displayName'] || 'User without a Name' }
  }

}
