import { Component, OnInit } from '@angular/core';
import { CourseDataService } from '../../course-data.service';
import { CourseService } from '../../../../data.service'
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-comments',
  templateUrl: './comments.component.html',
  styleUrls: ['./comments.component.css'],
  providers: [DatePipe]
})
export class CommentsComponent implements OnInit {
  comments:object[];
  currentCourse:string;
  newComment:object;
  // addCommentName:string = "Anonymous";
  addCommentDate:string;
  addCommentMsg:string;
  rating:number;
  usefulness:number;
  difficulty:number;
  enjoyment:number;
  selectedMajor:string;
  disableButton:boolean=false;
  submitConfirm:string='';
  colorChange:boolean;
  confirmSuccess:string = '';
  majors = [
      {name:"Anonymous"},
      {name:"Arts"},
      {name:"Accounting & Finance"},
      {name:"Business Management"},
      {name:"Engineering"},
      {name:"Architecture"},
      {name:"Computer Science"},
      {name:"Science"},
      {name:"Fashion"},
      {name:"Hospitality & Tourism"},
      {name:"Social Science"},
      {name:"Design"},
      {name:"Nursing"},
      {name:"English"},
      {name:"Journalism"},
      {name:"Math"},
      {name:"Performance"},
      {name:"Philosophy"},
      {name:"Psycology"},
      {name:"Communications"},
      {name:"Media"},
      {name:"Urban Planning"}
  ];

  constructor(private courseDataService: CourseDataService, private courseService: CourseService, private datePipe: DatePipe) { }

  ngOnInit(){

  }

  ngDoCheck() {
      this.currentCourse = this.courseDataService.getName();
      // console.log(this.courseDataService.getObject())
      // this.newComments = this.courseDataService.getObject().comments;
      // console.log(this.courseDataService.getName());

        if(this.isEmpty(this.comments)){
              for(var i:number = 0;i<this.courseService.courses.length;i++){
                  if(this.courseDataService.getName() == this.courseService.courses[i].name){
                      this.comments = this.courseService.courses[i].comments;
                      console.log(this.courseService.courses[i].comments);
                      // for(var n:number = 0;n<this.comments.length;n++){
                      //   console.log(this.comments[n].rating);
                      //   var temp:string = "star" + this.comments[n].rating;
                      //
                      // }

                  }
              }
        }


  }

    isEmpty(obj: object) {
        for(var key in obj) {
            if(obj.hasOwnProperty(key))
                return false;
        }
        return true;
    }

    onChange(value:any){
        this.selectedMajor = value;
    }

    addComment(){
console.log(this.courseDataService.getLogged());
        this.disableButton = true;
        if(this.courseDataService.getLogged()){
            this.confirmSuccess = '#39C16C';
            this.submitConfirm = "Your comment has been submitted!";
        }else{
            this.confirmSuccess = '#cc0000';
            this.submitConfirm = "Must log in to comment!";
        }

        if(this.rating == null){this.rating = 1;}
        if(this.usefulness == null){this.usefulness = 50;}
        if(this.difficulty == null){this.difficulty = 50;}
        if(this.enjoyment == null){this.enjoyment = 50;}
        if(this.selectedMajor == null){this.selectedMajor = "Anonymous";}

        var commentLength:number = 0;
        // console.log(this.rating);
        // console.log(this.usefulness);
        // console.log(this.difficulty);
        // console.log(this.enjoyment);

        for(var i:number = 0; i<this.courseService.courses.length;i++){
            if(this.courseService.courses[i].name == this.courseDataService.getName()){
                commentLength = this.courseService.courses[i].comments.length;
                if((this.courseService.courses[i].comments.length == 1) &&
                   (this.courseService.courses[i].comments[0].author == "Be the first to comment on this course!")){
                       commentLength = 0;
                   }
            }
        }

        var isVowel = this.selectedMajor.charAt(0);
        if( isVowel == "A" || isVowel == "E" || isVowel == "I" || isVowel == "O" || isVowel == "U"){
            this.selectedMajor = "Posted by an " + this.selectedMajor + " student";
        }else{
            this.selectedMajor = "Posted by a " + this.selectedMajor + " student";
        }

        this.addCommentDate = this.datePipe.transform(new Date());
        this.newComment = {author:this.selectedMajor,date:this.addCommentDate,message:this.addCommentMsg, rating:this.rating, usefulness:this.usefulness, difficulty:this.difficulty, enjoyment:this.enjoyment, user:this.courseDataService.getUser()};
        this.courseService.updateComment(this.courseDataService.getName(),commentLength,this.newComment);
        console.log(this.newComment);
    }

    ratingHandler(event){
        // console.log(event.target.id);

        switch(event.target.id){
            case "star1":{
                this.rating = 1;
                break;
            }
            case "star2":{
                this.rating = 2;
                break;
            }
            case "star3":{
                this.rating = 3;
                break;
            }
            case "star4":{
                this.rating = 4;
                break;
            }
            case "star5":{
                this.rating = 5;
                break;
            }
        }

    }

    isCheck1(x:any){
        if(x == 1){
            return true;
        }
    }
    isCheck2(x:any){
        if(x == 2){
            return true;
        }
    }
    isCheck3(x:any){
        if(x == 3){
            return true;
        }
    }
    isCheck4(x:any){
        if(x == 4){
            return true;
        }
    }
    isCheck5(x:any){
        if(x == 5){
            return true;
        }
    }

    usefulnessHandler(event){
        this.usefulness = +event.target.value;
        // console.log(this.usefulness);
    }

    difficultyHandler(event){
        this.difficulty = +event.target.value;
        // console.log(this.difficulty);
    }

    enjoymentHandler(event){
        this.enjoyment = +event.target.value;
        // console.log(this.enjoyment);
    }

    onDelete(selected:object){
        // console.log(selected);
              for(var i:number = 0;i<this.courseService.courses.length;i++){
                  if(this.courseDataService.getName() == this.courseService.courses[i].name){

                       for(var j:number = 0;j<this.courseService.courses[i].comments.length;j++){
                           if(this.courseService.courses[i].comments[j] == selected){
                               // console.log(this.courseService.courses[i].comments[j]);
                               this.courseService.deleteComment(this.courseDataService.getName(),this.courseService.courses[i].comments[j]);
                           }
                       }

                  }
              }
    }


}
