import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { AngularFireModule } from 'angularfire2';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { AngularFireAuthModule } from 'angularfire2/auth';

import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { CourseListComponent } from './dashboard/course-list/course-list.component';
import { CourseComponent } from './dashboard/course-list/course/course.component';
import { CourseDetailComponent } from './dashboard/course-list/course/course-detail/course-detail.component';
import { CourseDescriptionComponent } from './dashboard/course-list/course/course-detail/course-description/course-description.component';
import { CourseCommentsComponent } from './dashboard/course-list/course/course-detail/course-comments/course-comments.component';
import { Routes, RouterModule } from '@angular/router';
import { CourseDataService } from './dashboard/course-list/course-data.service';
import { SearchService } from './search.service';
import { CommentsComponent } from './dashboard/course-list/course/comments/comments.component';
import { environment } from '../environments/environment';
import { HomeComponent } from './home/home.component';
import { AboutComponent } from './about/about.component';

const appRoutes: Routes = [
    {path:'', component:HomeComponent},
    {path:'courses', component:CourseListComponent},
    {path:'about', component:AboutComponent},
    {path:'courses/:courseName', component:CourseComponent}
]

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    DashboardComponent,
    CourseListComponent,
    CourseComponent,
    CourseDetailComponent,
    CourseDescriptionComponent,
    CourseCommentsComponent,
    CommentsComponent,
    HomeComponent,
    AboutComponent
  ],
  imports: [
    FormsModule,
    BrowserModule,
    RouterModule.forRoot(appRoutes),
    AngularFireModule.initializeApp(environment.firebaseConfig),
    AngularFireDatabaseModule,
    AngularFireAuthModule
  ],
  providers: [CourseDataService, SearchService],
  bootstrap: [AppComponent]
})
export class AppModule { }
