import { Component, OnInit } from '@angular/core';
import { CourseService } from '../../data.service';
import { SearchService } from '../../search.service';
import { CourseDataService } from './course-data.service';

@Component({
  selector: 'app-course-list',
  templateUrl: './course-list.component.html',
  styleUrls: ['./course-list.component.css'],
  providers: [CourseService]
})
export class CourseListComponent implements OnInit {
  courses: object[];
  courseName:string;
  searchedCourses: object[] = [];
  isLoaded:boolean = false;

  constructor(private courseService: CourseService, private dataService: CourseDataService, private searchService: SearchService) {

  }

  ngOnInit() {
      this.courses = this.courseService.courses;
      console.log(this.courseService.courses);
  }

  selectCourse(course){
      this.dataService.updateObject(course);
      this.courseName=course.name;
      this.dataService.updateName(this.courseName);
  }

  ngDoCheck(){
      this.courses = this.searchService.getNewCourses();
      // if(this.searchedCourses != []){
      //   this.courses = this.searchedCourses;
      // }
      // console.log(this.courses);
      this.isLoaded = this.courseService.isLoaded;
  }

  selectTableA(){
      this.searchedCourses = [];

      for(var i:number = 0; i<this.courseService.courses.length;i++){
          if(this.courseService.courses[i].description.includes("Lower-level")){
              this.addSearchedCourse(this.courseService.courses[i]);
          }
      }
      console.log(this.searchedCourses);
      this.searchService.updateCategory(this.searchedCourses);
  }

  selectTableB(){
      this.searchedCourses = [];

      for(var i:number = 0; i<this.courseService.courses.length;i++){
          if(this.courseService.courses[i].description.includes("Upper-level")){
              this.addSearchedCourse(this.courseService.courses[i]);
          }
      }
      console.log(this.searchedCourses);
      this.searchService.updateCategory(this.searchedCourses);
  }

  addSearchedCourse(obj: object){
      this.searchedCourses.push(obj);
  }

}
