import { Component, OnInit } from '@angular/core';
import { CourseDataService } from '../../course-data.service';
import { ActivatedRoute } from '@angular/router';
import { CourseService } from '../../../../data.service'

@Component({
  selector: 'app-course-detail',
  templateUrl: './course-detail.component.html',
  styleUrls: ['./course-detail.component.css'],
  providers: [CourseService]
})
export class CourseDetailComponent implements OnInit {
  courseDetail: object = [];
  avgRating:number = 0;
  avgUsefulness:number = 0 ;
  avgDifficulty:number = 0;
  avgEnjoyment:number = 0;


  constructor(private courseService: CourseService,
              private dataService: CourseDataService,
              private route: ActivatedRoute) {

  }

  ngDoCheck(){
      // this.courseDetail = this.dataService.getObject();
      // console.log(this.courseService);

      if(this.isEmpty(this.courseDetail)){
          this.route.params.subscribe(params => {
              // console.log(params.courseName);
              this.dataService.updateName(params.courseName);

              for(var i:number = 0;i<this.courseService.courses.length;i++){
                  // console.log(this.courseService.courses[i].name);
                  if(params.courseName == this.courseService.courses[i].name){
                      this.courseDetail = this.courseService.courses[i];

                      console.log(this.courseService.courses[i].comments.length);

                      for(var j:number = 0;j<this.courseService.courses[i].comments.length;j++){
                          this.avgRating = this.avgRating + this.courseService.courses[i].comments[j].rating;
                          this.avgUsefulness = this.avgUsefulness + this.courseService.courses[i].comments[j].usefulness;
                          this.avgDifficulty = this.avgDifficulty + this.courseService.courses[i].comments[j].difficulty;
                          this.avgEnjoyment = this.avgEnjoyment + this.courseService.courses[i].comments[j].enjoyment;
                      }
                          this.avgRating = this.avgRating/this.courseService.courses[i].comments.length;
                          this.avgUsefulness = this.avgUsefulness/this.courseService.courses[i].comments.length;
                          this.avgDifficulty = this.avgDifficulty/this.courseService.courses[i].comments.length;
                          this.avgEnjoyment = this.avgEnjoyment/this.courseService.courses[i].comments.length;

                          this.avgRating = Math.round(this.avgRating * 100)/100;
                          this.avgUsefulness = Math.round(this.avgUsefulness);
                          this.avgDifficulty = Math.round(this.avgDifficulty);
                          this.avgEnjoyment = Math.round(this.avgEnjoyment);

                  }
              }

          });
      }
  }

  ngOnInit() {

  }

    isEmpty(obj: object) {
        for(var key in obj) {
            if(obj.hasOwnProperty(key))
                return false;
        }
        return true;
    }

}
